.contenedor-principal .contenedor-cuadricula-sinopsis .encabezado-cuadricula-sinopsis{
    display: flex;
    flex-flow: row nowrap;
    overflow: auto;
    height: 40px;
  }

  .contenedor-principal .contenedor-cuadricula-sinopsis .encabezado-cuadricula-sinopsis p{
      margin: 0px;
    flex-grow: 1;
    color: #5e5e5e;
    
  }

  .contenedor-principal .contenedor-cuadricula-sinopsis .encabezado-cuadricula-sinopsis hr{
    flex-grow: 10;
    border-left: none;
    height: 0.5px;
    border-color:rgb(255, 255, 255);
    background-color: #c2c2c2;
  }

  .contenedor-principal .contenedor-cuadricula-sinopsis .cuadricula-sinopsis{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: auto;
    grid-gap: 20px;
    grid-auto-rows: auto;

  }

  .contenedor-principal .contenedor-cuadricula-sinopsis .cuadricula-sinopsis .tarjeta-sinopsis{
    margin: 0px;
    max-width: auto !important;
  }

  @media (max-width:1500px){
    .contenedor-principal .contenedor-cuadricula-sinopsis .cuadricula-sinopsis{
      display: grid;
      grid-template-columns: repeat(2,1fr);
      grid-template-rows: auto;
      grid-gap: 20px;
  
    }
  }

  @media (max-width:576px){
    .contenedor-principal .contenedor-cuadricula-sinopsis .cuadricula-sinopsis{
      grid-template-columns: 1fr;      
  
    }
  }