  .contenedor-principal{
    border-radius: 5px;
    border: 1px solid #d4d4d4;
    background: #eee;
    min-height: 75vh;
    padding: 20px;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 300px auto 100px;
    grid-template-areas:    "recomendados   recomendados"
                              "ultimos        tops"
                              "palabras       palabras";
  }
  
  .contenedor-principal .recomendados{
    grid-area: recomendados;
  }
 

  .contenedor-principal .ultimos{
    grid-area: ultimos;
  
  }
  
  .contenedor-principal .tops{
    grid-area: tops;
  
  }
  
  .contenedor-principal .palabras{
    grid-area: palabras;
  
  }

  @media (max-width:1100px){
    .contenedor-principal{
      
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 300px auto auto 100px;
      grid-template-areas:    "recomendados   recomendados"
                                "ultimos      ultimos"
                                "tops         tops"
                                "palabras     palabras";
    }
  }

  