.contenedor-tarjeta-sinopsis{
    position: relative;
}

.tarjeta-sinopsis{
    border: 1px solid rgb(212, 212, 212);
    border-radius: 5px;
    height: auto;
    padding: 15px;
    background: #fff;  
    cursor: pointer;
    min-width: 200px;
    /*max-width: 350px;*/
    margin: 5px 20px 5px 5px;
    font-family:  Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    overflow: hidden;
    user-select: none;
}

.tarjeta-sinopsis h4{
    margin: 0px;
    text-align: center;
    height: 10%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.tarjeta-sinopsis div{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    line-height: 20px;    
    height: 200px;     
    -webkit-line-clamp: 9; /*Número de líneas a mostrarr*/
    -webkit-box-orient: vertical;
}

.tarjeta-sinopsis div p{
    font-family:  Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    /*height: 50px;

    overflow: hidden;
    text-overflow: ellipsis;
    
    white-space: normal;*/
    
}

@media (max-width: 430px){
    .tarjeta-sinopsis{        
        margin: 10px 6vw 10px 6vw;
        min-width: 68vw;
    }

    .tarjeta-sinopsis div{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        line-height: 20px;    
        max-height: 200px;     
        -webkit-line-clamp: 12; /*Número de líneas a mostrarr*/
        -webkit-box-orient: vertical;
    }
}