.barra-principal{
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    background: rgb(131, 62, 110);
    padding: .5rem 1rem;
    box-shadow: 0 -8px 15px 10px rgba(0,0,0,.5);
    z-index: 2;
  
  }

  .logo-barra{
    width: 150px;
    height: 35px;
    cursor: pointer;
  }
  
  .menu-usuario-barra{
    margin-left: auto;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    color: rgb(255, 255, 255);
    cursor: pointer;
    font-size: 0.9rem;
  }
  
.menu-usuario-barra-responsivo{
  display: none;
}

  .menu-usuario-barra div{
    padding:0.5rem 2rem;
    margin-right: 10px;
  }
  .menu-usuario-barra div:hover{
    transition: background-color 200ms ease-in;
    background-color: rgb(114, 54, 96);
  
  }
  .menu-usuario-barra .btn-registro-barra{
    outline: none;
    text-decoration: none;
    border: 2px solid rgb(202, 174, 98);
    border-radius: 5px;
    background: transparent;
    padding:0.6rem 1rem;
    cursor: pointer;
    color: rgb(202, 174, 98);
  
  }
  .menu-usuario-barra .btn-registro-barra:hover{
    transition: background-color 200ms ease-in;
    background-color: rgb(114, 54, 96);
  }

  .btn-sanguche-barra{
    display: none;
  }

  @media (max-width: 991px){
    .menu-usuario-barra{
      display: none;
    }
    .menu-usuario-barra-responsivo{
      display: flex;
      flex-flow: row wrap;
      justify-content: space-around;
      width: 80%;
      align-items: center;
    }
    
    .logo-barra{
      display: none;
    }

    .btn-sanguche-barra{
      display: block;
      margin-left: auto;
      cursor: pointer;
      outline: none;
    text-decoration: none;
    border: 2px solid rgb(202, 174, 98);
    border-radius: 5px;
    background: transparent;
    padding:0.4rem 0.8rem;
    cursor: pointer;
    color: rgb(202, 174, 98);
    }

    .btn-sanguche-barra:hover{
      transition: background-color 200ms ease-in;
      background-color: rgb(114, 54, 96);
    }
  }