.contenedor-principal .recomendados{
    position: relative;

  }
  .contenedor-principal .recomendados .encabezado-recomendados{
    display: flex;
    flex-flow: row nowrap;
    overflow: auto;
    height: 40px;
  }

  .contenedor-principal .recomendados .encabezado-recomendados p{
      margin: 0px;
    flex-grow: 1;
    color: #5e5e5e;
    
  }

  .contenedor-principal .recomendados .encabezado-recomendados hr{
    flex-grow: 10;
    border-left: none;
    height: 0.5px;
    border-color:rgb(255, 255, 255);
    background-color: #c2c2c2;
  }

  .contenedor-principal .recomendados .carrusel-recomendados{
    display: flex;
    flex-flow: row nowrap;
    overflow: hidden;
    height: 150%;
    scroll-behavior: smooth;
  }

  @media (max-width:768px){
    .contenedor-principal .recomendados .carrusel-recomendados{     
      overflow-x: auto;
      
    }
  }