.tarjeta-info-libro{
    /*animation: all 1s ease-in;*/
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    position: absolute;
    top: 270px;
    left: 10%;
    z-index: 10;
    border: 1px solid rgb(156, 156, 156);
    border-radius: 5px;
    height: auto;
    padding: 15px;
    justify-items: center;
    background: #fff;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 3;
    grid-template-rows: 3;
    grid-template-areas: "img-tarjeta-info-libro    nombres-tarjeta-info-libro    nombres-tarjeta-info-libro"
                         "est1-tarjeta-info-libro     est1-tarjeta-info-libro     est1-tarjeta-info-libro"
                         "est2-tarjeta-info-libro     est2-tarjeta-info-libro     est2-tarjeta-info-libro"
}

.tarjeta-info-libro img{
    grid-area: img-tarjeta-info-libro;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 2px solid rgb(210,210, 210);

    box-shadow: 0px 0px 12px -5px rgba(0,0,0,0.75);
}

.tarjeta-info-libro h1{
    grid-area: nombres-tarjeta-info-libro;
    font-size: 1rem;
}

.tarjeta-info-libro :nth-child(3){
    grid-area: est1-tarjeta-info-libro;
}

.tarjeta-info-libro :nth-child(4){
    grid-area: est2-tarjeta-info-libro;    

}
