.debajo-barra .menu-principal{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    font-size: 1rem;
    margin-bottom: 20px;
  }
  
  .debajo-barra .menu-principal div{
    padding: 0.3rem 3rem;
    cursor: pointer;
    border-radius: 20px;
    user-select: none;
    transition: background-color 200ms ease-in;

  }
  
  .debajo-barra .menu-principal div:hover{
    background-color: rgb(167, 79, 140);
    color: #fff;
  
  }

  .debajo-barra .menu-principal .item-menu-seleccionado{
    background-color: rgb(167, 79, 140);
    color: #fff;
  }

  @media (max-width: 991px){
    .debajo-barra .menu-principal div{
      padding: 0.3rem 2rem;

    }
  }

  @media (max-width: 991px){
    .debajo-barra .menu-principal{
       display: none;

    }
  }