p{
  font-size: 1rem;
  color: #929191;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  user-select: none;
}

.debajo-barra{
  margin-top: 55px;
  padding: 20px;
  min-height: 95vh;
  background: rgb(131,62,110);
background: linear-gradient(0deg, rgba(131,62,110,1) 0%, rgba(255,255,255,1) 15%, rgba(252,252,252,1) 100%);
z-index: 1;
}

