.seleccionable{
  transition: all 0.5s ease-in;
}

.seleccionable:hover{
  box-shadow: 0px 0px 12px -5px rgba(0,0,0,0.75);
  transform: scale(1.01, 1.01);

}

@media (max-width: 567px){
  .debajo-barra{
    padding: 0px;
  }
}